.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #6866f4;
  height: 60px;
}

/*
.sticky-nav {
    position: sticky;
    top: 0;
}
*/

.toolbar__navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.toolbar__logo {
  margin-left: 1rem;
}

.toolbar__logo a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
}

.toolbar__spacer {
  flex: 1;
}

.toolbar__navigation__items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.toolbar__navigation__items li {
  padding: 0 0.5rem;
}

.toolbar__navigation__items a {
  color: white;
  text-decoration: none;
}

.toolbar__navigation__items a:hover,
.toolbar__navigation__items a:active {
  color: #fa923f;
}

@media (max-width: 768px) {
  .toolbar__navigation__items {
    display: none;
  }
  .toolbar__logo {
    padding-top: 8px;
    padding-left: 20px;
    width: 80px;
    height: 60px;
    margin-left: 0;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle__button {
    display: none;
  }

  .toolbar__logo {
    padding-top: 6px;
    width: 60px;
    height: 60px;
    margin-left: 0;
  }
}
